header {
    height: auto;
    padding-top: 7rem;
    overflow: hidden;
}

.header_container {
    text-align: center;
    height: 100%;
    position: relative;
}

/*=============== CTA ===============*/

.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1rem;
    justify-content: center;
}

/*=============== HEADER SOCIAL ===============*/

.header_socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header_socials::after {
    content: "";
    width: 1px;
    height: 4rem;
    background: var(--color-primary);
}

/*=============== PHOTO PROFILE ===============*/

.me {
    /* background-color: var(--color-primary); */
    background: linear-gradient(180deg, var(--color-primary), transparent);
    width: 16rem;
    height: 21rem;
    position: relative;
    left: calc(50% - 8.3rem);
    margin-top: 4rem;
    border-radius: 20rem 20rem 0 0;
    overflow: hidden;
    padding: 3rem 0.5rem 0.5rem 0.5rem;
}

/*=============== SCROLL DOWN ===============*/

.scroll_down {
    position: absolute;
    right: -2.3rem;
    bottom: 3rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;

}

/* =============== MEDIA QUERIES (TABLET) =============== */

@media screen and (max-width: 1024px) {
    
}

/*=============== MEDIA QUERIES (PHONE) ===============*/

@media screen and (max-width: 600px) {

    .header_socials, .scroll_down {
        display: none;
    }
    
}